import * as React from 'react';
import { IconButton as IconButtons, Stack, Link } from 'office-ui-fabric-react';

const emojiIcon = { iconName: 'Emoji2' };

const menuProps = {
  items: [
    {
      key: 'emailMessage',
      text: 'Email message',
      iconProps: { iconName: 'Mail' }
    },
    {
      key: 'calendarEvent',
      text: 'Calendar event',
      iconProps: { iconName: 'Calendar' }
    }
  ],
  directionalHintFixed: true
};

export const IconButton = props => {
  const { disabled, checked } = props;

  return (
    <div>
      <Stack tokens={{ childrenGap: 8 }} horizontal>
        <IconButtons iconProps={emojiIcon} title="Emoji" ariaLabel="Emoji" disabled={disabled} checked={checked} />
        <IconButtons menuProps={menuProps} iconProps={emojiIcon} title="Emoji" ariaLabel="Emoji" disabled={disabled} checked={checked} />
      </Stack>
      <p>
        For a list of Icons, visit our <Link href="https://developer.microsoft.com/en-us/fabric#/styles/icons">Icon documentation</Link>.
      </p>
    </div>
  );
};
