import * as React from 'react';
import { CompoundButton as CompoundButtons, Stack } from 'office-ui-fabric-react';

// Example formatting
const stackTokens = { childrenGap: 40 };

export const CompoundButton = props => {
  const { disabled, checked } = props;

  return (
    <Stack horizontal tokens={stackTokens}>
      <CompoundButtons 
        secondaryText="This is the secondary text." 
        disabled={disabled} 
        checked={checked}>
        Standard
      </CompoundButtons>
      <CompoundButtons 
        primary 
        secondaryText="This is the secondary text." 
        disabled={disabled} 
        checked={checked}>
        Primary
      </CompoundButtons>
    </Stack>
  );
};