import * as React from 'react';
import { CommandBarButton as CommandBarButtons, Stack } from 'office-ui-fabric-react';

const menuProps = {
  items: [
    {
      key: 'emailMessage',
      text: 'Email message',
      iconProps: { iconName: 'Mail' }
    },
    {
      key: 'calendarEvent',
      text: 'Calendar event',
      iconProps: { iconName: 'Calendar' }
    }
  ]
};
const addIcon = { iconName: 'Add' };
const mailIcon = { iconName: 'Mail' };
const stackStyles = { root: { height: 44 } };

export const CommandBarButton= props => {
  const { disabled, checked } = props;

  // Here we use a Stack to simulate a command bar.
  // The real CommandBar control also uses CommandBarButtons internally.
  return (
    <Stack horizontal styles={stackStyles}>
      <CommandBarButtons
        iconProps={addIcon}
        text="New item"
        // Set split=true to render a SplitButton instead of a regular button with a menu
        // split={true}
        menuProps={menuProps}
        disabled={disabled}
        checked={checked}
      />
      <CommandBarButtons
        iconProps={mailIcon} 
        text="Send mail" 
        disabled={disabled} 
        checked={checked} />
    </Stack>
  );
};
