import React, { Suspense } from 'react';
import { BrowserRouter } from 'react-router-dom';
import { renderRoutes } from "react-router-config";

import { routes } from './routes'

export function RouteView() {
  return (
    <Suspense fallback={'loading...'}>
      <BrowserRouter>
        {renderRoutes(routes)}
      </BrowserRouter>
    </Suspense>
  )
}
