import * as React from 'react';
import { ActionButton as ActionButtons } from 'office-ui-fabric-react';

const addFriendIcon = { iconName: 'AddFriend' };

export const ActionButton = props => {
  const { disabled, checked } = props;

  return (
    <ActionButtons iconProps={addFriendIcon} allowDisabledFocus disabled={disabled} checked={checked}>
      Create account
    </ActionButtons>
  );
};
