import React  from 'react';
import { DefaultButton as DefaultButtons, PrimaryButton, Stack } from 'office-ui-fabric-react';

const stackTokens = { childrenGap: 40 };

export const DefaultButton = props => {
  const { disabled, checked } = props;

  return (
    <Stack horizontal tokens={stackTokens}>
      <DefaultButtons
        text="Standard"
        onClick={_alertClicked}
        disabled={disabled}
        checked={checked}
        allowDisabledFocus />
      <PrimaryButton
        text="Primary"
        onClick={_alertClicked}
        allowDisabledFocus
        disabled={disabled}
        checked={checked} />
    </Stack>
  )
}

function _alertClicked() {
  alert('Clicked');
}
